            <td>{{item.amount}}</td>
            <td>{{item.term}}</td>
            <td style="cursor: pointer; text-align: center" @click.prevent="deletes(item._id)"><img   src="img/delete.svg" width="15px"  alt=""></td>
          </template>
        </tr>
      </table>
    </div>
